#logobox2
{
    height: 62px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
#logobox2>p{
    font-size: 30px;
    font-family: Arial, Helvetica, sans-serif;
    color: #2D88E2;
    margin-left: 10px;
}

#menubox2
{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}

#menubox2>ol
{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    list-style-type: none;
    
}

#menubox2>ol>li
{
    text-decoration: none;
    color: #737890;
    cursor: pointer;
}

#menubox2>ol>li:hover
{
    color: black;
}

