#logobox
{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}

#logobox>p
{
    font-size: 22px;
    font-family: Arial, Helvetica, sans-serif;
    color: #2D88E2;
    margin-left: 10px;
}

#menubox
{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
}

#menubox>ol
{
    width: 100%;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
}

#menubox>ol>li>a
{
    text-decoration: none;
    color: #737890;
}

#menubox>ol>li>a:hover
{
    /* color: black; */
    color: #2D88E2;
}

#buttonbox
{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.btn{
   width: 150px;
   height: 35px;
   margin-left: 160px;
   justify-content: space-evenly;
   display: flex;
   align-items: center;
   font-weight: 550;
   color: white;
   background-color: #2D88E2;
   border-radius: 5px;
   border-style: none;
   cursor: pointer;
   letter-spacing: 1px;
}





